.dialogs {
  display: flex;

  .dialogItem {
    flex: 20%;
    margin: 10px;
    a.active {
      color: red;
    }
  }

  .message {
    flex: 80%;


  .messageItem {
    padding: 10px;
    margin: 10px;
    color: white;
    background-color: #0B93F6;
    border-radius: 15px;
  }
  }
}

.addPost {
  padding: 10px;
  textarea {
    padding: 10px;
    margin: 10px;
  }
  .errorMessage {
    color: red;
  }
  .error {
    border: 1px solid red;
  }
}


